@import './variables.scss';
@import './fonts.scss';
@import './common.scss';

.reflection-container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    background: linear-gradient(179.14deg, #0F4C81 3.38%, #5CAD8A 63.08%, #FFB347 131.35%);

    .container {
        max-width: 60vw;
        height: inherit;
        margin: 0 auto;

        h1 {
            font-size: 48px;
            font-family: 'Montserrat', sans-serif;
            color: white;
        }

        h2 {
            font-size: 26px;
            color: white;
            text-align: start;
            font-family: 'Montserrat', sans-serif;
        }

        .text-area-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            
            fieldset {
                width: 80%;
                border: 2px solid white;
                border-radius: 16px;
                text-align: start;
                padding-left: 30px;
                color: white;
                font-family: 'Montserrat Medium', sans-serif;
                font-size: 24px;
                //background-color: rgba(255, 255, 255, 0.3);
            }

            textarea {
                width: 100%;
                outline: none;
                border: none;
                background-color: transparent;
                color: white;
                resize: none; 
            }
        }

        .save-button {
            margin-top: 30px;
            background-color: transparent;
            border-radius: 10px;
            border: 2px solid white;
            color: white;
            font-size: 16px;
            cursor: pointer;
            font-family: 'Overpass', sans-serif;
            letter-spacing: 1px;
            padding: 10px 16px;
            margin-right: 10px;
            align-self: flex-end;
        }
    }
    .react-tooltip {
        background-color: $color_primary;
        color: white;
        font-family: 'Montserrat Medium', sans-serif;
        border-radius: 4px;
    }
}